<template>
  <div>
    <div
      class="tk-px-padding-px20 tk-px-padding-left-px0 tk-table-radio-button text-center"
    >
      <!-- <div
        class="tk-layout-inline-block ls-15 tk-font-color-main tk-px-margin-right-px10 tk-cursor-pointer"
      >
        Edit
      </div> -->
      <div
        class="tk-bg-color-gray-100 tk-border-color-gray-200 tk-px-radius-px40 tk-px-padding-px2 tk-layout-width-fit tk-layout-inline-block"
      >
        <button-radio-group
          :options="teacherListOptions"
          button-classes="btn-secondary tk-px-radius-px40"
          v-model="currentGroup"
        ></button-radio-group>
      </div>
    </div>
    <div class="card-wrapper">
      <div
        class="card tk-px-margin-bottom-px0 tk-layout-inline-block tk-no-shadow tk-border-color-gray-200"
        style="height: calc(100vh - 90px); width: calc(100% - 28px)"
      >
        <el-table
          class="table-responsive align-items-center table-flush tk-table-max-height"
          header-row-class-name="thead-light"
          :data="currentGroup == 'active' ? users : archive"
          ref="teacherList"
          id="teacherList"
        >
          <el-table-column min-width="160px" prop="name">
            <template v-slot="{ row }">
              <img
                :src="row.avatar"
                class="avatar rounded-circle mr-3 tk-cursor-pointer tk-layout-inline-block"
                @click.prevent="preview(row)"
              />
              <div
                class="tk-layout-inline-block"
                style="width: calc(100% - 48px - 1rem)"
              >
                <b
                  class="tk-text-wrap tk-cursor-pointer text-sm"
                  @click.prevent="preview(row)"
                  >{{ row.name }}</b
                >
                <div v-if="row.studentCount > 0">
                  <div
                    class="avatar-group tk-layout-inline-block"
                    style="opacity: 0.48"
                  >
                    <a
                      href="#"
                      class="avatar avatar-xs rounded-circle"
                      data-toggle="tooltip"
                      data-original-title="Ryan Tompson"
                    >
                      <img alt="Image placeholder" src="img/theme/team-4.jpg" />
                    </a>
                    <a
                      href="#"
                      class="avatar avatar-xs rounded-circle"
                      data-toggle="tooltip"
                      data-original-title="Romina Hadid"
                    >
                      <img alt="Image placeholder" src="img/theme/team-2.jpg" />
                    </a>
                    <a
                      href="#"
                      class="avatar avatar-xs rounded-circle"
                      data-toggle="tooltip"
                      data-original-title="Romina Hadid"
                    >
                      <img alt="Image placeholder" src="img/theme/team-3.jpg" />
                    </a>
                  </div>
                  <span
                    class="font-weight-bold tk-layout-inline-block tk-layout-prompt-text"
                  >
                    ({{ row.studentCount }})
                  </span>
                </div>
              </div>
            </template>
          </el-table-column>

          <el-table-column width="120px">
            <template v-slot="{ row }">
              <base-button
                size="sm"
                type="main"
                class="tk-font-color-white tk-layout-inline-block"
                v-if="row.studentCount == 0 && !row.needInvite"
                @click.prevent="addLesson(row)"
                >Add Lesson</base-button
              >
              <base-button
                size="sm"
                type="resend"
                class="tk-font-color-white tk-layout-inline-block"
                v-if="row.needInvite"
                @click.prevent="invite(row)"
                >Invite</base-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="tk-layout-inline-block tk-px-margin-left-px4">
        <letter-index
          :value="letter"
          @indexByLetter="indexByLetter"
        ></letter-index>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import bus from "@/bus";
import users from "./../teachers";
import archive from "./../archive";
import {
  Table,
  TableColumn,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Tooltip,
} from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    [Tooltip.name]: Tooltip,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    [Dropdown.name]: Dropdown,
    [DropdownItem.name]: DropdownItem,
    [DropdownMenu.name]: DropdownMenu,
    letterIndex: () => import("@/tkViews/components/layout/LetterIndex"),
    ButtonRadioGroup: () => import("@/components/ButtonRadioGroup.vue"),
  },
  data() {
    return {
      letter: "",
      users,
      archive,
      currentPage: 1,
      currentPage2: 1,
      isInvalid: false,
      deleteArchive: false,
      fullName: "",
      phone: "",
      email: "",
      url: "",
      index: -1,
      currentGroup: "active",
      teacherListOptions: [
        { value: "active", label: "Active" },
        { value: "archived", label: "Archived" },
      ],
    };
  },
  mounted() {
    let self = this;
    bus.$on("onActive", (data) => {
      self.onActive(data);
      console.log(self.archive);
    });
    bus.$on("onArchive", (data) => {
      self.onArchive(data);
    });
    bus.$on("confirmDelete", (data) => {
      if (data.active) {
        self.onDelete(data);
      } else {
        self.onDeleteArchive(data);
      }
      self.confirmDelete();
    });

    if (self.users.length > 0) {
      self.letter = self.users[0].name.substring(0, 1).toUpperCase();
    }

    $("#teacherList")
      .find(".el-table__body-wrapper")
      .eq(0)
      .scroll(() => {
        let $el = $("#teacherList").find(".el-table__body-wrapper").eq(0);
        let scrollTop = $el.scrollTop();
        let index = parseInt(scrollTop / 80);
        self.letter = self.users[index].name.substring(0, 1).toUpperCase();
      });

    self.initClick();
  },
  watch: {
    currentGroup(newVal, oldVal) {
      setTimeout(() => {
        this.initClick();
      }, 500)
    },
  },
  methods: {
    initClick() {
      let self = this;

      let teacherList = document.getElementById("teacherList");
      let ul_child = teacherList.getElementsByClassName("el-table__row");
      for (var i = 0; i < ul_child.length; i++) {
        let index = i;
        ul_child[i].onclick = (ev) => {
          if (ev.target.localName !== "button") {
            $(".tk-selected").removeClass("tk-selected");
            ul_child[index].classList.add("tk-selected");
            self.preview(self.users[index]);
          }
        };
      }
    },
    addLesson(data) {
      let self = this;
      console.log(data);
    },
    invite(data) {
      let self = this;
      console.log(data);
    },
    indexByLetter(data) {
      let self = this;
      let letter = data.letter;
      // console.log(letter)
      let letterIndex = 0;
      for (let i = 0; i < self.users.length; i++) {
        if (self.users[i].name.substring(0, 1).toUpperCase() == letter) {
          letterIndex = i;
          break;
        }
      }
      $("#teacherList")
        .find(".el-table__body-wrapper")
        .eq(0)
        .scrollTop(letterIndex * 80);
    },
    preview(data) {
      this.$emit("preview", data);
    },
    onDelete(row) {
      let self = this;
      self.users.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
        }
      });
    },
    onDeleteArchive(row) {
      let self = this;
      self.archive.some((item, i) => {
        if (item.id === row.id) {
          self.index = i;
          self.deleteArchive = true;
        }
      });
    },
    confirmDelete() {
      let self = this;
      if (self.deleteArchive) {
        self.archive.splice(self.index, 1);
        self.deleteArchive = false;
      } else {
        self.users.splice(self.index, 1);
      }
      self.index = -1;
    },
    onArchive(row) {
      // let self = this;
      // self.users.some((item, i) => {
      //   if (item.id === row.id) {
      //     self.users.splice(i, 1);
      //     self.archive.push(row);
      //   }
      // });
    },
    onActive(row) {
      // let self = this;
      // self.archive.some((item, i) => {
      //   if (item.id === row.id) {
      //     self.archive.splice(i, 1);
      //     self.users.push(row);
      //   }
      // });
    },
  },
};
</script>
